//Variables
const input = document.getElementById("user-input")
const button = document.getElementById("submit-button")
const selfieVector = document.getElementById("selfie-vector")
const container = document.getElementById("first-container")
const Clarifai = require('clarifai')
//Security issue, key should be placed on backend. Key below is free so to keep app simple im going to leave it for now
const app = new Clarifai.App({ apiKey: 'e252e9b73e3b4b528a83cf349ad8d3f1'});
//Set initial value for opacity for fadeout
selfieVector.style.opacity = 1

//Functions
calculateFaceLocation = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box
    const image = document.getElementById("newimage")
    return {
        leftCol: clarifaiFace.left_col * image.width,
        topRow: clarifaiFace.top_row * image.height,
        rightCol: image.width - (clarifaiFace.right_col * image.width),
        bottomRow: image.height - (clarifaiFace.bottom_row * image.height)
    }
}

//API specified syntax, posts input to API and gets response
const handleApiCall = (input) => {
    app.models.predict(Clarifai.FACE_DETECT_MODEL, input)
        .then(response=> {
            const boxData = calculateFaceLocation(response)
            let box = document.createElement('div')
            box.className = ('face-box')
            box.style.top = `${boxData.topRow}px`
            box.style.right =  `${boxData.rightCol}px`
            box.style.left = `${boxData.leftCol}px`
            box.style.bottom = `${boxData.bottomRow}px`
            document.getElementById('image-container').appendChild(box)            
        })
        .catch(err => res.status(400).json('Unable to work with API')
    )
}

const send = () => {
    if(input.value.length > 0) {
        handleApiCall(input.value)
    }
}

const fadeout= (element) => {    
    let io = element.style.opacity;  // Initial opacity
    let timer = setInterval(function () {
        if (io <= 0.1){
            clearInterval(timer);
            io = 0
        }
        element.style.opacity = io;
        element.style.filter = 'alpha(opacity=' + io * 100 + ")";
        io -= io * 0.1;
    }, 10);
}

const fadein= (element) => {
    let io = 0.1;  // Initial opacity
    let timer = setInterval(function () {
        if (io >= 1){
            clearInterval(timer);    
        }
        element.style.opacity = io;
        element.style.filter = 'alpha(opacity=' + io * 100 + ")";
        io += io * 0.1;
    }, 10);
}

const addNewPicture = () => {
    if(input.value.length > 0) {
        let imageContainer = document.createElement('div')
        imageContainer.id = ('image-container')
        imageContainer.style.position = 'relative'
        imageContainer.style.marginLeft = '50px'
        container.appendChild(imageContainer)
        //Need to create separate div for picture + box on top
        let facePic = document.createElement("IMG")
        facePic.src = `${input.value}`
        facePic.alt = 'Whaat ? Try link with .jpg or .png extention'
        facePic.id = "newimage"
        facePic.classList.add('newpic')
        facePic.width="488" 
        facePic.style.opacity = 0
        facePic.style.fontSize = '25px'
        facePic.style.deco
        selfieVector.style.display = 'none'
        document.getElementById('image-container').appendChild(facePic)
        fadein(facePic)
    }
}
//Events
button.addEventListener("click",function(){
    let test = document.getElementsByClassName('newpic')
    if(test.length < 1) {    
        addNewPicture()
        send()
    } else {
        if (input.value.length > 0)
            document.getElementById('image-container').remove()    
            addNewPicture()
            send()
    } 
})

input.addEventListener('keypress', function(event){
    if(event.keyCode === 13) {
        let test = document.getElementsByClassName('newpic')
        if(test.length < 1) {
            addNewPicture()
            send()
        } else {
            if (input.value.length > 0)
            document.getElementById('image-container').remove()
                addNewPicture()
                send()
        }       
    }  
})

input.addEventListener('input', function(){
    if(input.value.length > 0) {
        fadeout(selfieVector)
    } else {
        fadein(selfieVector)
    }
})